.hero-homepage {
    background-image: 
        url(../images/undp-logo-30.svg),
	    url(../images/gef.png), 
        url(../images/state-emblem-pk.svg), 
    	url(../images/barley.jpg);

    background-size: 
    	auto 12%,
    	auto 12%,
        auto 12%,
    	cover;

    background-clip: border-box;
    background-repeat: no-repeat;

    background-position: 
        top 20% right 6%,
        top 40% right 5%,
        top 60% right 5%,
        top 0% left 0%;
}