h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: bold;	
	text-shadow: 2px 2px 3px rgba(255,255,255,0.5);
}

p.content {
	text-align: justify;
}

.section-heading {
	@extend .title;
	@extend .is-3;
	color: #008542 !important;
	font-weight: normal !important;
	border-left: 0.5rem solid #008542;
	padding-left: 1rem;
	text-transform: uppercase;


	&.margin-top {
		margin-top: 3rem;
		
		&-2x {
			margin-top: 4rem;
		}
	}

	&.margin-bottom {
		margin-bottom: 1.5rem;	

		&-2x {
			margin-bottom: 3rem;
		}
	}
}